<template>
    <div id="navbar">
        <nav class="uk-navbar-container" uk-navbar uk-sticky>
            <div class="uk-navbar-center">

                <div class="uk-navbar-center-left">
                    <ul class="uk-navbar-nav">
                        <li>
                            <a href="/index.html">Top</a>
                        </li>
                        <li>
                            <!-- <a href="/service-and-products.html">Service & Products</a> -->
                        </li>
                    </ul>
                </div>

                <div class="uk-navbar-item uk-logo">
                    <!-- <div class="uk-navbar-item uk-logo" uk-toggle="target: #offcanvas-nav"> -->
                    <img src="@/assets/Logo.svg">
                </div>

                <div class="uk-navbar-center-right">
                    <ul class="uk-navbar-nav">
                        <li><a href="/articles.html">Articles</a></li>
                        <li><a href="/about-us.html">About Us</a></li>
                    </ul>
                </div>
            </div>
        </nav>
        <div id="offcanvas-nav" uk-offcanvas="overlay: true">
            <div class="uk-offcanvas-bar">
                <button class="uk-offcanvas-close" type="button" uk-close></button>
                <ul class="uk-nav uk-nav-default">
                    <li class="uk-active"><a href="#">Active</a></li>
                    <li class="uk-parent">
                        <a href="#">Parent</a>
                        <ul class="uk-nav-sub">
                            <li><a href="#">Sub item</a></li>
                            <li><a href="#">Sub item</a></li>
                        </ul>
                    </li>
                    <li class="uk-nav-header">Header</li>
                    <li><a href="#"><span class="uk-margin-small-right" uk-icon="icon: table"></span> Item</a></li>
                    <li><a href="#"><span class="uk-margin-small-right" uk-icon="icon: thumbnails"></span> Item</a>
                    </li>
                    <li class="uk-nav-divider"></li>
                    <li><a href="#"><span class="uk-margin-small-right" uk-icon="icon: trash"></span> Item</a></li>
                </ul>

            </div>
        </div>
    </div>
</template>

<script>
    class QueryWalker {
        constructor(oDict, rootElement) {
            this.oDict = oDict;
            this.rootElement = rootElement;
        }

        async do(selectorTarget) {
            if (!selectorTarget) {
                selectorTarget = this.rootElement;
            }
            for (let selector in this.oDict) {
                let elements = selectorTarget.querySelectorAll(selector);
                for (let e of elements) {
                    try {
                        this.oDict[selector](e);
                    } catch (ex) {
                        console.warn("QueryWalker |", ex);
                    }
                }
            }
        }
    }

    export default {
        name: 'NavBar',
        mounted() {
            let queryWalker = new QueryWalker(
                {
                    ".uk-navbar-container li > a": async (e) => {
                        if (
                            e.getAttribute("href") ==
                            ("/" == window.location.pathname
                                ? "/index.html"
                                : window.location.pathname)
                        ) {
                            e.parentNode.setAttribute("class", "uk-active");
                        }
                    },
                    "[hide-sp]": async (e) => {
                        if (window.innerWidth < window.innerHeight) {
                            e.style.display = "none";
                        }
                    },
                },
                document.querySelector("#navbar")
            );
            queryWalker.do();
        }
    }
</script>
<style scoped>
    nav .uk-logo>img {
        width: 40px;
    }

    nav .uk-logo:hover {
        filter: drop-shadow(0px 0px 1px #666);
