<template>
    <div>
    </div>
</template>

<script>

    let shuffle = (arr) => {
        let i, j, tmp, length;

        for (length = arr.length, i = length - 1; i > 0; i--) {
            j = Math.floor(Math.random() * (i + 1));
            tmp = arr[i];
            arr[i] = arr[j];
            arr[j] = tmp;
        }

        return arr;
    }

    let animate_string = async (target, str, blind, wait) => {
        let rtc = Array.from(
            new Set(
                shuffle(
                    str.split(""))));

        for (let i = 1; i <= str.length; i++) {
            let r = [];
            for (let c of str) {
                if (rtc.slice(0, i).includes(c)) {
                    r.push(c);
                } else {
                    r.push(blind);
                }
            }

            target(r.join(""));
            let sleep = msec => new Promise(resolve => setTimeout(resolve, msec));
            await sleep(wait);
        }
    }


    async function main() {
        [...document.querySelectorAll("[anim_string]")].forEach((e) => {
            animate_string(
                x => e.textContent = x,
                e.textContent,
                e.getAttribute("anim_blind"),
                (Number)(e.getAttribute("amim_wait"))
            );
        });
    }

    export default {
        name: 'AnimateString',
        mounted() {
            main();
        }
    }

</script>

